  }),
  metaInfo: {
    title: 'Người đàn bà tiết liệt - Truyện Dân Gian',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_58393890_nguoi-dan-ba-tiet-liet.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Người đàn bà tiết liệt' },
      { name: 'Description', content: 'Ngày xưa ở vùng Bắc Ninh là đất nổi tiếng có nhiều con gái đẹp, có một cô gái làng Cách bi, về làm vợ ông cử nhân Nguyên Hanh, giữ chức tri huyện Thủy đường, người ta vẫn thường gọi là bà huyện Thủy đường. Lấy chồng sinh được...' },

      { itemprop: 'name', content: 'Truyện Dân Gian' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện Dân Gian, Chuyện Dân Gian' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

